<template>
    <div>
        <v-snackbar multi-line v-for="(item, index) in items" :key="index" v-model="item.show" :timeout="item.timeout" :color="item.color">
            <div class="text-subtitle-1 font-weight-bold">
                <template v-if="item.icon">
                    <v-icon left>{{ item.icon }}</v-icon>
                </template>
                {{ item.title }}
            </div>
            <p class="mb-0">{{ item.text }}</p>
        </v-snackbar>
    </div>
</template>

<script>
export default {
    name: 'MyNotification',

    data() {
        return {
            items: [],
        };
    },

    methods: {
        showNotification({ text, title, timeout = 5000, color = 'success', icon = '' }) {
            this.items.push({ text, title, timeout, color, icon, show: true });
        },
    },
};
</script>