                                    </v-btn>
                                    <v-btn small
                                           elevation="0"
                                           color="#9C27B0"
                                           @click="ShowCoordsSelector = false;ClickButton('CopyCurrentCoords','table')">
                                        {x = x, y = y, z = z}
                                    </v-btn>
                                </v-card-actions>
