                            <v-img contain :src="`https://docs.fivem.net/peds/${SelectedPedName !== '' ? SelectedPedName : 'mp_m_freemode_01'}.webp`" alt="Description de l'image" :style="{ 'height': '100%', 'max-height': '150px', 'width': '100%', 'max-width': '150px' }"/>
