                        >
                            Infinite Stamina
                        </v-btn>
                    </v-col>
                </v-row>
            </v-col>
        </v-row>

        <h2 class="text-center mt-10">Vehicle 🏎️</h2>
        <v-divider class="border-opacity-100 mb-10 mt-2 rounded-lg" color="#1976D2"></v-divider>

        <v-row>
            <v-col cols="6">
                <v-row>
                    <v-col cols="4">
                        <v-btn small
                               elevation="0"
                               width="100%"
                               color="#1976D2"
                               @click="ClickButton('DeleteCurrentVehicle')">
                            Delete
                        </v-btn>
                    </v-col>
                    <v-col cols="4">
                        <v-btn small
                               elevation="0"
                               width="100%"
                               color="#1976D2"
                               @click="ClickButton('RepairCurrentVehicle')">
                            Repair
                        </v-btn>
                    </v-col>
                    <v-col cols="4">
                        <v-btn small
                               elevation="0"
                               width="100%"
                               color="#1976D2"
                               @click="ClickButton('FlipCurrentVehicle')">
                            Flip
                        </v-btn>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col cols="4">
                        <v-btn small
                               elevation="0"
