                        </v-btn>
                        <v-dialog v-model="ColorPickerDialog" @input="ClickButton('ChangeCurrentVehicleColor')" max-width="300px">
                            <v-color-picker v-model="selectedVehicleColor" hide-header hide-inputs></v-color-picker>
                        </v-dialog>

                    </v-col>
                    <v-col cols="4">
                        <v-btn small
