                                style="color: #1976D2"
                            ></span ></h5>
                            <v-slider
                                v-model="CurrentVehicleSpeedBoost"
                                max="100"
                                step="1"
                                min="1"
                                color="#1976D2"
                                hide-details
                                :thumb-size="20"
                                class="ml-3 mr-3"
                                @input="updateSpeedBoost"
                                @change="ClickButton('VehicleSpeedBoost')"
                            >
                            </v-slider>
                        </v-card>
                    </v-col>
                    <v-col cols="6">
                        <v-card>
                            <h5 class="text-center pt-2 mb-0">GRAVITY BOOST: <span
                                v-text="`x${CurrentVehicleGravityBoost}`"
                                class="font-weight-bold"
                                style="color: #1976D2"
                            ></span ></h5>
                            <v-slider
                                v-model="CurrentVehicleGravityBoost"
                                max="10.0"
                                step="0.1"
                                min="1.0"
                                color="#1976D2"
                                hide-details
                                class="ml-3 mr-3"
                                @input="updateGravityBoost"
                                @change="ClickButton('VehicleGravityBoost')"
                            >
                            </v-slider>
                        </v-card>
                    </v-col>
                </v-row>
            </v-col>
            <v-divider class="border-opacity-100 rounded-lg" color="#1976D2" vertical />
            <v-col cols="6">
                <v-row>
                    <v-col cols="6">
                        <v-select
                            v-model="SelectedVehicleName"
                            :items="Vehicles || VehiclesList"
                            placeholder="🔎 Vehicle Model..."
                            :menu-props="{ maxHeight: '200px',auto: false, nudgeTop: 0 }"
                            dense
                            outlined
                            class="custom-border"
                            width="100%"
                            no-details
                        />
                        <v-checkbox v-model="SpawnInside" dense label="Spawn Inside" color="#1976D2" inset></v-checkbox>
                        <v-btn small
                               class="mt-0"
                               elevation="1"
                               width="100%"
                               height="40"
                               color="#1976D2"
                               @click="ClickButton('SpawnVehicle')">SPAWN VEHICLE
                        </v-btn>
                    </v-col>
                    <v-col cols="6">
                        <v-card elevation="0" class="rounded-l d-flex justify-center align-center" style="border: 1px solid #1976D2;" width="175" max-width="175" height="165px" max-height="165px" outlined>
                            <v-img contain :src="`https://docs.fivem.net/vehicles/${SelectedVehicleName}.webp`" alt="Description de l'image" :style="{ 'height': '100%', 'max-height': '150px', 'width': '100%', 'max-width': '150px' }" />
                        </v-card>
                    </v-col>
                </v-row>

            </v-col>
        </v-row>

        <h2 class="text-center mt-10">Weapons 🔫</h2>
        <v-divider class="border-opacity-100 mb-10 mt-2 rounded-lg" color="#1976D2"></v-divider>

        <v-row>
            <v-col cols="6">
                <v-row>
                    <v-col cols="4">
                        <v-btn small
                               elevation="0"
                               width="100%"
                               color="#1976D2"
                               @click="ClickButton('RefillWeaponAmmo')">
                            REFILL AMMO
                        </v-btn>
                    </v-col>
                    <v-col cols="4">
                        <v-menu
                            v-model="TintMenu"
                            :close-on-content-click="true"
                            offset-y
                            height="175" max-height="175"
                        >
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn
                                    v-bind="attrs"
                                    v-on="on"
                                    small
                                    elevation="0"
                                    width="100%"
                                    color="#1976D2"
                                >
                                    SET TINT
                                </v-btn>
                            </template>
                            <v-list v-if="PlayerWeapon">
                                <v-list-item
                                    v-for="(item, index) in AvailableTints[PlayerWeapon.includes('_mk2') ? 'Tints_MK2' : 'Tints']"
                                    :key="index"
                                    :value="item"
                                    @click="ClickButton('SetWeaponTint',item.Tint)"
                                    dense
                                >
                                    <v-list-item-title>{{item.Label}}</v-list-item-title>
                                </v-list-item>
                            </v-list>
                        </v-menu>
                    </v-col>
                    <v-col cols="4">
                        <v-menu
                            v-model="ComponentMenu"
                            :close-on-content-click="false"
                            offset-y
                            height="175" max-height="175"
                        >
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn
                                    v-bind="attrs"
                                    v-on="on"
                                    small
                                    elevation="0"
                                    width="100%"
                                    color="#1976D2"
                                >
                                    SET COMPONENTS
                                </v-btn>
                            </template>
                            <v-list v-if="PlayerWeapon">
                                <v-list-item
                                    v-for="(item, index) in WeaponComponents[PlayerWeapon]"
                                    :key="index"
                                    :value="item"
                                    @click="ClickButton('SetWeaponComponent')"
                                    dense
                                >
                                    <v-checkbox
                                        v-model="item.Selected"
                                        :label="item.Label"
                                    ></v-checkbox>
                                </v-list-item>
                            </v-list>
                        </v-menu>
                    </v-col>
                </v-row>
                <v-row>
                    <v-col cols="12">
                        <v-card width="100%">
                            <h5 class="text-center pt-2 mb-0">DAMAGE BOOST: <span
                                v-text="`x${CurrentWeaponDamageBoost}`"
                                class="font-weight-bold"
                                style="color: #1976D2"
                            ></span ></h5>
                            <v-slider
                                v-model="CurrentWeaponDamageBoost"
                                max="10.0"
                                step="0.1"
                                min="1.0"
                                color="#1976D2"
                                hide-details
                                class="ml-3 mr-3"
                                @input="updateDamageBoost"
                                @change="ClickButton('DamageBoost')"
                            >
                            </v-slider>
                        </v-card>
                    </v-col>
                </v-row>
            </v-col>
            <v-divider class="border-opacity-100 rounded-lg" color="#1976D2" vertical />
            <v-col cols="6">
                <v-row>
                    <v-col cols="6">
                        <v-select
                            v-model="SelectedWeaponName"
                            :items="WeaponsList"
                            placeholder="🔎 Weapon Model..."
                            :menu-props="{ maxHeight: '200px',auto: false, nudgeTop: 0 }"
                            dense
                            outlined
                            class="custom-border"
                            width="100%"
                            no-details
                        />
                        <v-checkbox v-model="EquipWeapon" dense label="Equip Weapon" color="#1976D2" inset></v-checkbox>
                        <v-checkbox v-model="SpawnWithAmmo" dense label="Spawn With Ammo" color="#1976D2" inset></v-checkbox>

                        <v-btn small
                               class="mt-0"
                               elevation="1"
                               width="100%"
                               height="40"
                               color="#1976D2"
                               @click="ClickButton('GiveWeapon')">SPAWN WEAPON
                        </v-btn>
                    </v-col>
                    <v-col cols="6">
                        <v-card elevation="0" class="rounded-l d-flex justify-center align-center" style="border: 1px solid #1976D2;" width="175" max-width="175" height="165px" max-height="165px" outlined>
                            <v-img contain :src="`https://docs.altv.mp/gta/images/weapon/models/${SelectedWeaponName !== '' ? SelectedWeaponName : 'weapon_unarmed'}.png`" alt="Description de l'image" :style="{ 'height': '100%', 'max-height': '150px', 'width': '100%', 'max-width': '150px' }" />
                        </v-card>
                    </v-col>
                </v-row>

            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import { mapState } from "vuex";
import api from "@/api/axios";
export default {
    name: 'SelfTab',
    data() {
        return {
            ShowCoordsSelector: false,
            SelectedPedName: "",

            ColorPickerDialog: false,
            selectedVehicleColor: { r: 255, g: 0, b: 255, a: 1 },

            SpawnInside: false,
            SelectedVehicleName: "",

            TintMenu: false,
            ComponentMenu: false,

            EquipWeapon: true,
            SpawnWithAmmo: true,
            SelectedWeaponName: "",
        };
    },
    computed: {
        ...mapState(["PedsList","Vehicles","VehiclesList","WeaponsList","AvailableTints","WeaponComponents","PlayerWeapon","FastRun", "NoClip", "GodMode","Invisible", "NoRagdoll","NoCollisions", "InfiniteStamina","CurrentWeaponDamageBoost","CurrentVehicleSpeedBoost","CurrentVehicleGravityBoost"]),
    },
    methods: {
        updateFastRun(value) {
            this.$store.commit('updateFastRun', value);
        },
        updateSpeedBoost(value) {
            this.$store.commit('updateSpeedBoost', value);
        },
        updateGravityBoost(value) {
            this.$store.commit('updateGravityBoost', value);
        },
        updateDamageBoost(value) {
            this.$store.commit('updateDamageBoost', value);
        },
        async ToggleButton(buttonName) {
            this.$store.commit('updateToggle', buttonName);
            await api.post(buttonName, {Type: "Self"});
        },
        async ClickButton(buttonName, extra) {
            if (buttonName === "SpawnVehicle") {
                await api.post(buttonName, {Type: "Self", VehicleName: this.SelectedVehicleName, SpawnInside: this.SpawnInside});
            } else if (buttonName === "GiveWeapon") {
                if (this.EquipWeapon === true) {
                    this.$store.commit('updatePlayerWeapon', this.SelectedWeaponName);
                }
                await api.post(buttonName, {Type: "Self", WeaponName: this.SelectedWeaponName, SpawnWithAmmo: this.SpawnWithAmmo, Equip: this.EquipWeapon});
            } else if (buttonName === "VehicleSpeedBoost") {
                await api.post(buttonName, {Type: "Self", SpeedBoost: this.CurrentVehicleSpeedBoost.toFixed(1)});
            } else if (buttonName === "DamageBoost") {
                await api.post(buttonName, {Type: "Self", DamageBoost: this.CurrentWeaponDamageBoost.toFixed(1)});
            } else if (buttonName === "VehicleGravityBoost") {
                await api.post(buttonName, {Type: "Self", GravityBoost: this.CurrentVehicleGravityBoost.toFixed(1)});
            } else if (buttonName === "ChangeCurrentVehicleColor") {
                await api.post(buttonName, {Type: "Self", Colors: this.selectedVehicleColor});
            } else if (buttonName === "SetWeaponTint") {
                await api.post(buttonName, {Type: "Self", Tint: extra});
            } else if (buttonName === "ChangePedModel") {
                await api.post(buttonName, {Type: "Self", PedModel: this.SelectedPedName});
            } else if (buttonName === "SetWeaponComponent") {
                await api.post(buttonName, {Type: "Self", Components: this.WeaponComponents[this.PlayerWeapon]});
            } else if (buttonName === "CopyCurrentCoords") {
                await api.post(buttonName, {Type: "Self", CopyType: extra});
            } else if (buttonName === "FastRun") {
                await api.post(buttonName, {Type: "Self", SpeedBoost: this.FastRun.toFixed(1)});
            } else {
                await api.post(buttonName, {Type: "Self"});
            }
        },
    }
}
</script>
<style >
.v-input__icon {
    display: none;
}
.v-list-item--dense, .v-list--dense .v-list-item {
    height:20px;
    min-height: 20px;
}
.v-menu__content {
    border: 1px solid #1976D2
}

.v-slider__thumb {
    height: 20px;
    width: 20px;
    margin-top: 3px;
}

.v-slider__thumb::before, .v-slider__thumb::after {
    margin-top: 4px;
    margin-left: 4px;
}

.theme--dark.v-slider .v-slider__track-background, .theme--dark.v-slider .v-slider__track-fill {
    height: 10px;
    border-radius: 10px;
}
</style>