<template>
    <div>
        <v-row>
            <v-col cols="6" class="text-left">
                <h2>Online Players 😃</h2>
                <h5 class="font-weight-light">{{PlayerCount}}/{{ MaxPlayerCount }} Players</h5>
            </v-col>
            <v-col cols="3" class="text-right">
                <v-text-field v-model="searchText" label="Search" dense @input="searchPlayers">
                    <template v-slot:prepend-inner>
                        <v-icon>mdi-magnify</v-icon>
                    </template>
                </v-text-field>
            </v-col>
            <v-col cols="3" class="text-right">
                <v-select v-model="selectedSort" :items="sortOptions" label="Sort by" dense prepend-inner-icon="mdi-email-outline">
                    <template v-slot:prepend-inner>
                        <v-icon>mdi-filter</v-icon>
                    </template>
                    <template #selection="{ item }">{{ item.label }}</template>
                    <template #item="{ item }">
                        <v-list-item @click="sortBy(item.value)">
                            <v-list-item-title>{{ item.label }}</v-list-item-title>
                        </v-list-item>
                    </template>
                </v-select>
            </v-col>
        </v-row>

        <v-divider class="border-opacity-100 mb-5 mt-2 rounded-lg" color="#1976D2"></v-divider>

        <v-row class="mb-1">
            <v-col v-for="(player, idx) in FilteredPlayers" :key="idx" cols="4">
                <v-card
                    @mouseover="hoveredPlayerId = player.ID"
                    @mouseout="hoveredPlayerId = null"
                    @click="showModal[player.ID].show = true;handleModalStatus(true)"
                    :class="{ 'card-hover': hoveredPlayerId === player.ID}"
                >
                    <v-card-subtitle>
                        <v-icon
                            class="mr-1"
                            size="150%"
                            :color="playerHealthColor(player.Health, player.Action)"
                        >
                            {{ playerIcon(player.Action) }}
                        </v-icon>
                        [{{ player.ID }}] {{ player.Admin ? "🛡️ " : "" }}
                        <span style="color: white">{{ player.Name }}</span> -
                        {{ player.Distance }}m
                    </v-card-subtitle>
                    <v-row>
                        <v-col cols="12" class="d-flex">
                            <v-progress-linear
                                rounded
                                :color="playerHealthColor(player.Health, player.Action)"
                                :height="5"
                                v-model="player.Health"
                                class="mx-auto"
                                style="width: 90%;"
                            ></v-progress-linear>
                        </v-col>
                    </v-row>
                </v-card>
                <PlayerModal
                    :player-name="`${player.Admin ? '🛡️ ' : ''} ${player.Name}`"
                    :player-data="player"
                    :dialog="showModal[player.ID].show"
                    :player-id="player.ID"
                    @dialog-closed="showModal[player.ID].show = $event;handleModalStatus($event)"
                />
            </v-col>
        </v-row>
    </div>
</template>

<script>
import {mapState} from "vuex";
import PlayerModal from '../Components/PlayerModal.vue'
import api from "@/api/axios";

export default {
    name: 'PlayersTab',
    components: {
        PlayerModal
    },
    data() {
        return {
            FilteredPlayers: {},
            showModal: {} ,
            hoveredPlayerId: null,
            searchText: "",
            selectedSort: "idAsc",
            sortOptions: [
                { value: 'idAsc', label: 'ID (Joined First)' },
                { value: 'idDesc', label: 'ID (Joined Last)' },
                { value: 'distAsc', label: 'Distance (Closest)' },
                { value: 'distDesc', label: 'Distance (Farthest)' },
            ],
        };
    },
    computed: {
        ...mapState(["PlayerCount","Players","MaxPlayerCount"]),
    },
    methods: {
        async handleModalStatus(status) {
            await api.post("PlayerModalStatus", status);
        },
        playerIcon(action) {
            if (action === "dead") {
                return 'mdi-skull-outline';
            } else if (action === "connecting") {
                return 'mdi-connection';
            } else if (action === "automobile") {
                return 'mdi-car-outline';
            } else if (action === "bike") {
                return 'mdi-bike';
            } else if (action === "boat") {
                return 'mdi-sail-boat';
            } else if (action === "heli") {
                return 'mdi-helicopter';
            } else if (action === "plane") {
                return 'mdi-airplane';
            } else if (action === "submarine") {
                return 'mdi-submarine';
            } else if (action === "trailer") {
                return 'mdi-truck-trailer';
            } else if (action === "train") {
                return 'mdi-train';
            } else if (action === "weapon") {
                return 'mdi-pistol';
            } else {
                return 'mdi-walk';
            }
        },
        playerHealthColor(Health, Action) {
            if (Action === "connecting") {
                return "grey"
            } else if (Health <= 25) {
                return "red";
            } else if (Health <= 75) {
                return "orange";
            } else {
                return "green";
            }

        },
        sortBy: function(sortOrder) {
            this.selectedSort = sortOrder;
            switch(sortOrder) {
                case "idAsc":
                    this.FilteredPlayers = Object.values(this.FilteredPlayers).sort(function(a, b) { return a.ID - b.ID; });
                    break;
                case "idDesc":
                    this.FilteredPlayers = Object.values(this.FilteredPlayers).sort(function(a, b) { return b.ID - a.ID; });
                    break;
                case "distAsc":
                    this.FilteredPlayers = Object.values(this.FilteredPlayers).sort(function(a, b) { return a.Distance - b.Distance; });
                    break;
                case "distDesc":
                    this.FilteredPlayers = Object.values(this.FilteredPlayers).sort(function(a, b) { return b.Distance - a.Distance; });
                    break;
                default:
                    break;
            }
        },
        searchPlayers() {
            const filteredPlayers = {};
            for (const playerId in this.Players) {
                const player = this.Players[playerId];
                if (player.Name.toLowerCase().includes(this.searchText.toLowerCase())) {
                    filteredPlayers[playerId] = player;
                }
            }
            this.FilteredPlayers = filteredPlayers;
        }
    },
    watch: {
        Players: {
            handler: function () {
                //this.$forceUpdate()
                this.FilteredPlayers = this.Players
                this.searchPlayers()
                this.sortBy(this.selectedSort)
            },
            deep: true,
        },
        PlayerCount: {
            handler: function () {
                //this.$forceUpdate()
            },
        },
    },
    created() {
        this.FilteredPlayers = this.Players
        for (let id = 0; id <= 1000; id++) {
            if (!this.showModal[id.toString()] || this.showModal[id.toString()].show !== true) {
                this.$set(this.showModal, id.toString(), { show: false });
            }
        }
    },
}
</script>

<style scoped>
.card-hover {
    transform: scale(1.05);
    transition: all 0.2s ease-in-out;
    background-color: #333333;
}
</style>
