<template>
    <v-container fluid>
        <div v-if="IsWaveShieldStarted">

        </div>

        <div>
            <v-row>
                <v-col cols="6" class="text-left">
                    <h2>Server Bans 🚫</h2>
                    <h5 class="font-weight-light">Banned Players: {{Bans.length}}</h5>
                </v-col>
                <v-col cols="3" class="text-right">
                    <v-text-field v-model="searchBanID" label="Search Reason/Ban ID" dense @input="searchBans">
                        <template v-slot:prepend-inner>
                            <v-icon>mdi-magnify</v-icon>
                        </template>
                    </v-text-field>
                </v-col>
                <v-col cols="3" class="text-right">
                    <v-select v-model="selectedSort" :items="sortOptions" label="Sort by" dense prepend-inner-icon="mdi-email-outline">
                        <template v-slot:prepend-inner>
                            <v-icon>mdi-filter</v-icon>
                        </template>
                        <template #selection="{ item }">{{ item.label }}</template>
                        <template #item="{ item }">
                            <v-list-item @click="sortBy(item.value)">
                                <v-list-item-title>{{ item.label }}</v-list-item-title>
                            </v-list-item>
                        </template>
                    </v-select>
                </v-col>
            </v-row>

            <v-divider class="border-opacity-100 mb-5 mt-2 rounded-lg" color="#1976D2"></v-divider>
        </div>

        <v-row class="mb-1">
            <v-col v-for="(ban, idx) in FilteredBans" :key="idx" cols="4">
                <v-card>
                    <v-card-subtitle>
                        <v-icon
                            class="mr-1"
                            size="150%"
                            color="red"
                        >mdi-cancel</v-icon>
                        <span style="color: white;font-weight: bold">{{ ban.name }}</span>
                    </v-card-subtitle>
                    <v-card-text class="code" style="user-select: text">
                        Ban ID: {{ban.id}}<br>
                        Reason: {{ban.reason}}<br>
                        <span v-if="ban.extended" v-html="'Details: ' + ban.extended + '<br>'"></span>
                        Expiration: {{getHumanDiffs(ban.expires)}}
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn v-if="ban.identifiers" small elevation="0" color="#1976D2" @click="idsModal[idx].show = true">See IDs</v-btn>
                        <v-btn v-if="ban.screenshot" small elevation="0" color="#1976D2" @click="imageModal[idx].show = true">IMAGE</v-btn>
                        <v-btn small elevation="0" color="#f44336" @click="Unban(ban.id)">UNBAN</v-btn>

                        <v-dialog v-model="idsModal[idx].show" max-width="500">
                            <v-card>
                                <v-card-title>Player Identifiers</v-card-title>
                                <v-card-text>
                                    <pre style="user-select:text;">{{ JSON.stringify(ban.identifiers, null, 2) }}</pre>
                                </v-card-text>
                                <v-card-actions>
                                    <v-btn color="primary" @click="idsModal[idx].show = false">Close</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>

                        <v-dialog v-model="imageModal[idx].show" max-width="500">
                            <v-card>
                                <v-card-title>Player Screenshot</v-card-title>
                                <v-card-text>
                                    <v-img :src="ban.screenshot" class="img-fluid"></v-img>
                                </v-card-text>
                                <v-card-actions>
                                    <v-btn color="primary" @click="imageModal[idx].show = false">Close</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>
                    </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import {mapState} from "vuex";
import moment from 'moment';
import api from "@/api/axios";

export default {
    name: 'WaveShieldTab',
    data() {
        return {
            idsModal: [],
            imageModal: [],

            FilteredBans: [],
            searchBanID: "",
            selectedSort: "",
            sortOptions: [
                { value: 'expAsc', label: 'Expiration (Closest)' },
                { value: 'expDesc', label: 'Expiration (Farthest)' },
            ],
        }
    },
    computed: {
        ...mapState(["Bans", "IsWaveShieldStarted"]),
    },
    watch: {
        Bans: {
            handler: function () {
                //this.$forceUpdate()
                this.FilteredBans = this.Bans
                this.searchBans()
                this.sortBy(this.selectedSort)
            },
            deep: true,
        },
    },
    methods: {
        async Unban(banId) {
            await api.post("UnbanPlayer", {Type: "Self", BanId: banId});
        },
        getHumanDiffs(expires) {
            const date = new Date(expires * 1000);
            return moment(date).fromNow();
        },
        sortBy: function(sortOrder) {
            this.selectedSort = sortOrder;
            switch(sortOrder) {
                case "expAsc":
                    this.FilteredBans.sort(function(a, b) { return a.expires - b.expires; });
                    break;
                case "expDesc":
                    this.FilteredBans.sort(function(a, b) { return b.expires - a.expires; });
                    break;
            }
        },
        searchBans() {
            this.FilteredBans = this.Bans.filter((ban) => {
                return ban.id.toString().toLowerCase().includes(this.searchBanID.toLowerCase()) ||
                    ban.name.toLowerCase().includes(this.searchBanID.toLowerCase()) ||
                    ban.reason.toLowerCase().includes(this.searchBanID.toLowerCase());
            });
        }
    },
    created() {
        this.FilteredBans = this.Bans
        this.idsModal = this.FilteredBans.map(() => ({ show: false }))
        this.imageModal = this.FilteredBans.map(() => ({ show: false }))
    },
}
</script>