<template>
	<v-container fluid fill-height>
		<v-row justify="center" align="center">
			<v-card style="overflow: hidden;" elevation="5" width="1000px" maxWidth="1000px" height="700px" maxHeight="700px" class="px-5 py-5 rounded-lg">


				<v-card-title class="pt-0 mb-5" style="position: fixed">
					<v-toolbar-title absolute>
						<v-img
								src="https://media.discordapp.net/attachments/746079909910151328/1094056163483598929/PNG-2.png?width=683&height=683"
								height="48"
								max-height="48"
								width="48"
								max-width="48"
								class="mr-10"
								contain>
						</v-img>
					</v-toolbar-title>

					<v-btn
							elevation="1"
							class="text-none font-weight-bold mr-5"
							:color="ActiveTab === 'self' ? '#1976D2' : ''"
							@click="setActiveTab('self')"
					><v-icon size="16" class="mr-1 ">mdi-face-man-shimmer</v-icon>SELF</v-btn>

					<v-btn
							elevation="1"
							class="text-none font-weight-bold mr-5"
							:color="ActiveTab === 'players' ? '#1976D2' : ''"
							@click="setActiveTab('players')"
					><v-icon size="16" class="mr-1 ">mdi-account-search-outline</v-icon>PLAYERS</v-btn>

					<v-btn
							elevation="1"
							class="text-none font-weight-bold mr-5"
							:color="ActiveTab === 'visuals' ? '#1976D2' : ''"
							@click="setActiveTab('visuals')"
					><v-icon size="16" class="mr-1 ">mdi-target</v-icon>VISUALS</v-btn>

					<v-btn
							elevation="1"
							class="text-none font-weight-bold mr-5"
							:color="ActiveTab === 'server' ? '#1976D2' : ''"
							@click="setActiveTab('server')"
					><v-icon size="16" class="mr-1 ">mdi-cog-outline</v-icon>SERVER</v-btn>

					<v-btn
							elevation="1"
							class="text-none font-weight-bold mr-5"
							:color="ActiveTab === 'reports' ? '#1976D2' : ''"
							@click="setActiveTab('reports')"
					><v-icon size="16" class="mr-1 ">mdi-bug-outline</v-icon>REPORTS</v-btn>

					<v-btn
							v-if="IsWaveShieldStarted == true"
							elevation="1"
							class="text-none font-weight-bold mr-5"
							:color="ActiveTab === 'waveshield' ? '#1976D2' : ''"
							@click="setActiveTab('waveshield')"
					><v-icon size="16" class="mr-1 ">mdi-shield-half-full</v-icon>WAVESHIELD</v-btn>

					<v-btn
							v-if="IsWaveShieldStarted != true"
							elevation="1"
							class="text-none font-weight-bold mr-5"
							:color="ActiveTab === 'bans' ? '#1976D2' : ''"
							@click="setActiveTab('bans')"
					><v-icon size="16" class="mr-1 ">mdi-cancel</v-icon>BANS</v-btn>
				</v-card-title>

				<div style="display: flex; flex-direction: column; height: 100%; padding-top: 80px;">
					<v-card elevation="0" class="px-5 py-5 rounded-lg" style="display: inline-block;overflow-y: auto !important;" color="#262626">
						<SelfTab v-if="ActiveTab === 'self'" />
						<PlayersTab v-if="ActiveTab === 'players'" />
						<VisualsTab v-if="ActiveTab === 'visuals'" />
						<ServerTab v-if="ActiveTab === 'server'" />
						<ReportsTab v-if="ActiveTab === 'reports'" />
						<BansTab v-if="ActiveTab === 'bans'" />
						<WaveShieldTab v-if="ActiveTab === 'waveshield'" />
					</v-card>
				</div>

			</v-card>
		</v-row>
	</v-container>
</template>

<script>
	import api from "../api/axios";
	import SelfTab from './Tabs/Self.vue'
	import PlayersTab from "@/views/Tabs/Players.vue";
	import VisualsTab from "@/views/Tabs/Visuals.vue";
	import ServerTab from "@/views/Tabs/Server.vue";
	import ReportsTab from "@/views/Tabs/Reports.vue";
	import BansTab from "@/views/Tabs/Bans.vue";
	import WaveShieldTab from "@/views/Tabs/WaveShield.vue";
	import {mapState} from "vuex";

	export default {
		components: {
			VisualsTab,
			ServerTab,
			PlayersTab,
			ReportsTab,
			BansTab,
			WaveShieldTab,
			SelfTab
		},
		name: "NavBar",
		data() {
			return {
				colorOptions: ["success", "error", "warning", "primary"],
				typeOfMessage: "",
				error: "",
				userName: "",
				message: "",
			};
		},
		computed: {
			...mapState(["ActiveTab",'IsWaveShieldStarted']),
		},
		mounted() {
			document.addEventListener('keyup', this.onKeyup)
		},
		beforeUnmount() {
			document.removeEventListener('keyup', this.onKeyup)
		},
		methods: {
			setActiveTab(tabName) {
				this.$store.commit('updateActiveTab', tabName);
			},
			async onKeyup(event) {
				if (event.keyCode === 27) {
					await api.post("exitMenu");
				}
			},
		},
	};
</script>

<style scoped>
.previewLayout {
	display: flex;
	align-items: center;
	gap: 1rem;
}
