<template>
    <v-container fluid>
        <h2 class="text-center ">Reports 📝</h2>
        <h5 class="text-center font-weight-light">Active Reports: {{ Reports.length }} | Non-taken Reports: {{ FreeReports }}</h5>
        <v-divider class="border-opacity-100 mb-3 mt-2 rounded-lg" color="#1976D2"></v-divider>
        <v-row class="mb-1">
            <v-col v-for="(report, idx) in Reports" :key="idx" cols="4">
                <v-card>
                    <v-card-subtitle>
                        <v-icon
                            class="mr-1"
                            size="150%"
                            :color="report.TakenBy == null ? 'green' : 'red'"
                        >mdi-help-circle</v-icon>
                        <span style="color: white;font-weight: bold">[{{report.ID}}] {{ report.Name }}</span>
                        <span v-if="report.TakenBy" style="font-weight: bold"> | Taken by: {{report.TakenBy}}</span>
                    </v-card-subtitle>
                    <v-card-text class="code" style="user-select: text">
                        Message: {{report.Reason}}<br>
                        <span v-if="report.extended" v-html="'Details: ' + report.extended + '<br>'"></span>
                        Created: {{getHumanDiffs(report.CreatedAt)}}
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn v-if="report.TakenBy == null" small elevation="0" color="#4CAF50" @click="Take(report.ID)">Take</v-btn>
                        <v-btn small elevation="0" color="#1976D2" @click="idsModal[idx].show = true">Actions</v-btn>
                        <v-btn small elevation="0" color="#f44336" @click="Close(report.ID)">CLOSE</v-btn>

                        <v-dialog v-model="idsModal[idx].show" max-width="500">
                            <v-card>
                                <v-card-title>Report Actions</v-card-title>
                                <v-card-text>
                                    <pre style="user-select:text;">Name: [{{report.ID}}] {{ report.Name }}<br>Message: {{ report.Reason }}<br>Taken By: {{report.TakenBy}}<br>Created: {{getHumanDiffs(report.CreatedAt)}}
                                    </pre>
                                </v-card-text>
                                <v-card-actions class="d-flex justify-center">
                                    <v-row>
                                        <v-col cols="4">
                                            <v-btn small
                                                   class="mb-5"
                                                   elevation="0"
                                                   width="100%"
                                                   color="#1976D2"
                                                   @click="ClickReportButton('GoTo',report.ID)">
                                                GO TO
                                            </v-btn>
                                            <v-btn small
                                                   class="mb-5"
                                                   elevation="0"
                                                   width="100%"
                                                   color="#1976D2"
                                                   @click="ClickReportButton('RevivePlayer', report.ID)">
                                                REVIVE
                                            </v-btn>
                                            <v-select
                                                v-model="SelectedTPLocation"
                                                :items="Object.keys(QuickTeleportLocations)"
                                                placeholder="🔎 Location..."
                                                :menu-props="{ maxHeight: '200px',auto: false, nudgeTop: 0 }"
                                                dense
                                                outlined
                                                class="custom-border"
                                                width="100%"
                                                no-details
                                            />
                                        </v-col>
                                        <v-col cols="4">
                                            <v-btn small
                                                   class="mb-5"
                                                   elevation="0"
                                                   width="100%"
                                                   color="#1976D2"
                                                   @click="ClickReportButton('Bring',report.ID)">
                                                BRING
                                            </v-btn>
                                            <v-btn small
                                                   class="mb-5"
                                                   elevation="0"
                                                   width="100%"
                                                   color="#1976D2"
                                                   @click="ClickReportButton('RefillHealth', report.ID)">
                                                HEAL
                                            </v-btn>
                                            <v-btn small
                                                   class="mb-5"
                                                   elevation="0"
                                                   width="100%"
                                                   color="#1976D2"
                                                   @click="ClickReportButton('TeleportTo',report.ID)">
                                                Teleport To
                                            </v-btn>
                                        </v-col>
                                        <v-col cols="4">
                                            <v-btn small
                                                   class="mb-5"
                                                   elevation="0"
                                                   width="100%"
                                                   color="#1976D2"
                                                   @click="ClickReportButton('BringBack',report.ID)">
                                                BRING BACK
                                            </v-btn>
                                            <v-btn small
                                                   class="mb-5"
                                                   elevation="0"
                                                   width="100%"
                                                   color="#1976D2"
                                                   @click="showGiveVehicle = true">
                                                GIVE VEHICLE
                                            </v-btn>
                                            <dialog-input
                                                title="Give Vehicle"
                                                placeholder="Vehicle Name"
                                                :value="GiveVehicleName"
                                                :dialog="showGiveVehicle"
                                                @input="ClickReportButton('SpawnVehicle', report.ID)"
                                                @dialog-closed="showGiveVehicle = $event"
                                            />
                                        </v-col>
                                        <v-col cols="12">
                                            <v-btn small
                                                   class="mb-5"
                                                   elevation="0"
                                                   width="100%"
                                                   color="#f44336"
                                                   @click="idsModal[idx].show = false">
                                                CLOSE
                                            </v-btn>
                                        </v-col>
                                    </v-row>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>
                    </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import {mapState} from "vuex";
import moment from "moment/moment";
import api from "@/api/axios";
import DialogInput from "@/views/Components/DialogInput.vue";

export default {
    name: 'ReportsTab',
    components: {DialogInput},
    data() {
        return {
            FreeReports: 0,
            idsModal: [],
            SelectedTPLocation: "",
            GiveVehicleName: "",
            showGiveVehicle: false,
        }
    },
    computed: {
        ...mapState(["Reports","QuickTeleportLocations"]),
    },
    watch: {
        Reports: {
            handler: function () {
                //this.$forceUpdate()
                //this.sortReports()
                //this.countTakenReports()
            },
            deep: true,
        },
    },
    methods: {
        async ClickReportButton(buttonName, reportID) {
            if (buttonName === "SpawnVehicle") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: reportID,
                    SpawnInside: true,
                    VehicleName: this.GiveVehicleName,
                });
            } else if (buttonName === "TeleportTo") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: reportID,
                    Location: this.SelectedTPLocation,
                });
            } else {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: reportID,
                });
            }
        },
        async Close(ID) {
            await api.post("CloseReport", {Type: "Self", ID: ID});
        },
        async Take(ID) {
            await api.post("TakeReport", {Type: "Self", ID: ID});
        },
        getHumanDiffs(expires) {
            const date = new Date(expires * 1000);
            return moment(date).fromNow();
        },
        sortReports() {
            this.Reports = this.Reports.sort((a, b) => a.CreatedAt - b.CreatedAt);
        },
        countTakenReports() {
            let takenReportsCount = 0;
            for (let i = 0; i < this.Reports.length; i++) {
                if (this.Reports[i].TakenBy !== null) {
                    takenReportsCount++;
                }
            }
            this.FreeReports = takenReportsCount
        }
    },
    created() {
        this.sortReports()
        this.countTakenReports()
        this.idsModal = this.Reports.map(() => ({ show: false }))
    },
}
</script>