<template>
    <v-container fluid>
        <h2 class="text-center ">ESP 🎯</h2>
        <v-divider class="border-opacity-100 mb-3 mt-2 rounded-lg" color="#1976D2"></v-divider>
        <v-row>
            <v-col cols="4">
                <v-switch
                    v-model="BoxESP"
                    hide-details
                    inset
                    label="Box ESP"
                    @change="ToggleButton2('BoxESP')"
                ></v-switch>
                <v-switch
                    v-model="LineESP"
                    hide-details
                    inset
                    label="Line ESP"
                    @change="ToggleButton2('LineESP')"
                ></v-switch>
                <v-switch
                    v-model="SelfESP"
                    hide-details
                    inset
                    label="Include Self"
                    @change="ToggleButton2('SelfESP')"
                ></v-switch>
            </v-col>
            <v-col cols="4">
                <v-switch
                    v-model="FillBox"
                    hide-details
                    inset
                    label="Fill Box"
                    @change="ToggleButton2('FillBox')"
                ></v-switch>
                <v-switch
                    v-model="PlayersBlip"
                    hide-details
                    inset
                    label="Players Blip"
                    @change="ToggleButton2('PlayersBlip')"
                ></v-switch>
            </v-col>
            <v-col cols="4">
                <v-switch
                    v-model="SkeletonESP"
                    hide-details
                    inset
                    label="Skeleton ESP"
                    @change="ToggleButton2('SkeletonESP')"
                ></v-switch>
                <v-switch
                    v-model="PlayersName"
                    hide-details
                    inset
                    label="Players Name"
                    @change="ToggleButton2('PlayersName')"
                ></v-switch>
            </v-col>
        </v-row>
        <v-row>
            <v-col cols="12">
                <v-card>
                    <h5 class="text-center pt-2 mb-0">DISTANCE ESP: <span
                        v-text="`${DistanceESP}m`"
                        class="font-weight-bold"
                        style="color: #1976D2"
                    ></span ></h5>
                    <v-slider
                        v-model="DistanceESP"
                        max="1000"
                        step="1"
                        min="100"
                        color="#1976D2"
                        hide-details
                        class="ml-3 mr-3"
                        @input="updateDistanceESP"
                        @change="ClickButton2('DistanceESP')"
                    >
                    </v-slider>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import {mapState} from "vuex";
import api from "@/api/axios";

export default {
    name: 'VisualsTab',
    computed: {
        ...mapState(["BoxESP", "LineESP","SelfESP", "FillBox", "PlayersBlip", "SkeletonESP", "PlayersName","DistanceESP"]),
    },
    methods: {
        async ClickButton2(buttonName) {
            if (buttonName === "DistanceESP") {
                await api.post(buttonName, {Type: "Self", Distance: this.DistanceESP.toFixed(1)});
            } else {
                await api.post(buttonName, {Type: "Self"});
            }
        },
        updateDistanceESP(value) {
            this.$store.commit('updateDistanceESP', value);
        },
        async ToggleButton2(buttonName) {
            this.$store.commit('updateToggle', buttonName);
            await api.post(buttonName, {Type: "Self"});
        },
    }
}
</script>