<template>
    <v-dialog v-model="dialogValue" max-width="500">
        <v-card clas>
            <v-card-title>{{ title }}</v-card-title>
            <v-card-text>
                <v-text-field v-model="inputText" :placeholder="placeholder">
                    <template v-slot:prepend-inner>
                        <v-icon>mdi-pencil-outline</v-icon>
                    </template>
                </v-text-field>
            </v-card-text>
            <v-card-actions class="ml-2">
                <v-btn class="mr-1" color="error" @click="close">CANCEL</v-btn>
                <v-btn color="#1976D2" @click="save">SUBMIT</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
export default {
    props: {
        title: String,
        value: String,
        dialog: Boolean,
        placeholder: String,
    },
    data() {
        return {
            dialogValue: false,
            inputText: ""
        };
    },
    watch: {
        dialog: function(val) {
            this.dialogValue = val;
            if (this.dialogValue) {
                this.inputText = this.value;
            }
        },
        value: function(val) {
            this.inputText = val;
        },
        dialogValue: function(val) {
            if (!val) {
                this.$emit("dialog-closed");
            }
        }
    },
    methods: {
        save() {
            this.$emit("input", this.inputText);
            this.dialogValue = false;
        },
        close() {
            this.dialogValue = false;
        }
    }
};
</script>