<template>
    <v-container fluid>
        <h2 class="text-center ">Actions 💥</h2>
        <v-divider class="border-opacity-100 mb-5 mt-2 rounded-lg" color="#1976D2"></v-divider>
        <v-row>
            <v-col cols="4">
                <v-btn small
                       class="mb-5"
                       elevation="0"
                       width="100%"
                       color="#1976D2"
                       @click="showAnnounce = true">
                    SERVER ANNOUNCE
                </v-btn>
                <dialog-input
                    :title="AnnounceDialogTitle"
                    :placeholder="AnnouncePlaceHolder"
                    :value="TextAnnouncement"
                    :dialog="showAnnounce"
                    @input="handleAnnounceText"
                    @dialog-closed="showAnnounce = $event"
                />
            </v-col>
            <v-col cols="4">
                <v-btn small
                       class="mb-5"
                       elevation="0"
                       width="100%"
                       color="#1976D2"
                       @click="ClickButton3('ReviveAllPlayers')">
                    REVIVE ALL
                </v-btn>
            </v-col>
            <v-col cols="4">
                <v-btn small
                       class="mb-5"
                       elevation="0"
                       width="100%"
                       color="#1976D2"
                       @click="showKickReason = true">
                    KICK ALL
                </v-btn>
                <dialog-input
                    :title="KickReasonDialogTitle"
                    :placeholder="KickReasonPlaceHolder"
                    :value="KickReason"
                    :dialog="showKickReason"
                    @input="handleKickReason"
                    @dialog-closed="showKickReason = $event"
                />

            </v-col>
        </v-row>

        <h2 class="text-center mt-10">Map Cleaner 🌍</h2>
        <v-divider class="border-opacity-100 mb-5 mt-2 rounded-lg" color="#1976D2"></v-divider>
        <v-row>
            <v-col cols="4">
                <v-btn small
                       class="mb-5"
                       elevation="0"
                       width="100%"
                       color="#1976D2"
                       @click="ClickButton3('DeleteAllObjects')">
                    DELETE ALL OBJECTS
                </v-btn>
                <v-btn small
                       elevation="0"
                       width="100%"
                       color="#1976D2"
                       @click="showClearMapRadius = true">
                    CLEAR MAP AROUND
                </v-btn>
                <dialog-input
                    :title="ClearMapRadiusDialogTitle"
                    :placeholder="ClearMapRadiusPlaceHolder"
                    :value="ClearMapRadius"
                    :dialog="showClearMapRadius"
                    @input="handleClearMapRadius"
                    @dialog-closed="showClearMapRadius = $event"
                />
            </v-col>
            <v-col cols="4">
                <v-btn small
                       class="mb-5"
                       elevation="0"
                       width="100%"
                       color="#1976D2"
                       @click="ClickButton3('DeleteAllPeds')">
                    DELETE ALL PEDS
                </v-btn>
                <v-btn small
                       elevation="0"
                       width="100%"
                       color="#1976D2"
                       @click="ClickButton3('DeleteDeadPeds')">
                    DELETE DEAD PEDS
                </v-btn>
            </v-col>
            <v-col cols="4">
                <v-btn small
                       class="mb-5"
                       elevation="0"
                       width="100%"
                       color="#1976D2"
                       @click="ClickButton3('DeleteUnseatedVehicles')">
                    DELETE UNSEATED VEHICLES
                </v-btn>
                <v-btn small
                       elevation="0"
                       width="100%"
                       color="#1976D2"
                       @click="ClickButton3('DeleteBrokenVehicles')">
                    DELETE BROKEN VEHICLES
                </v-btn>
            </v-col>
        </v-row>
        <h2 class="text-center mt-10">World ⛅</h2>
        <v-divider class="border-opacity-100 mb-5 mt-2 rounded-lg" color="#1976D2"></v-divider>
        <v-row>
            <v-col cols="4">
                <v-select
                    v-model="SelectedWeatherName"
                    :items="WeathersList"
                    placeholder="🔎 Change Weather..."
                    :menu-props="{ maxHeight: '200px',auto: false, nudgeTop: 0 }"
                    dense
                    outlined
                    class="custom-border mb-3"
                    width="100%"
                    no-details
                />
                <v-btn small
                       class="mb-5"
                       elevation="0"
                       width="100%"
                       color="#1976D2"
                       @click="ClickButton3('SetWeather')">
                    SET WEATHER
                </v-btn>
                <v-btn small
                       elevation="0"
                       width="100%"
                       :color="FreezeWeather ? '#4CAF50' : '#f44336'"
                       @click="ToggleButton3('FreezeWeather')">
                    FREEZE WEATHER
                </v-btn>
            </v-col>
            <v-col cols="4">
                <v-select
                    v-model="SelectedTimeName"
                    :items="TimesList"
                    placeholder="🔎 Change Time..."
                    :menu-props="{ maxHeight: '200px',auto: false, nudgeTop: 0 }"
                    dense
                    outlined
                    class="custom-border mb-3"
                    width="100%"
                    no-details
                />
                <v-btn small
                       class="mb-5"
                       elevation="0"
                       width="100%"
                       color="#1976D2"
                       @click="ClickButton3('SetTime')">
                    SET TIME
                </v-btn>
                <v-btn small
                       elevation="0"
                       width="100%"
                       :color="FreezeTime ? '#4CAF50' : '#f44336'"
                       @click="ToggleButton3('FreezeTime')">
                    FREEZE TIME
                </v-btn>
            </v-col>
            <v-col cols="4">
                <v-btn small
                       class="mb-5"
                       elevation="0"
                       width="100%"
                       :color="Blackout ? '#4CAF50' : '#f44336'"
                       @click="ToggleButton3('Blackout')">
                    BLACKOUT
                </v-btn>
                <v-btn small
                       elevation="0"
                       width="100%"
                       color="#1976D2"
                       @click="ClickButton3('EarthQuake')">
                    EARTHQUAKE
                </v-btn>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import {mapState} from "vuex";
import api from "@/api/axios";
import DialogInput from '../Components/DialogInput.vue'

export default {
    name: 'ServerTab',
    components: {
        DialogInput
    },
    data() {
        return {
            SelectedWeatherName: "",
            SelectedTimeName: "",

            AnnounceDialogTitle: "Enter a message",
            AnnouncePlaceHolder: "Text...",
            showAnnounce: false,
            TextAnnouncement: "",

            KickReasonDialogTitle: "Enter a kick reason",
            KickReasonPlaceHolder: "Reason...",
            showKickReason: false,
            KickReason: "",

            ClearMapRadiusDialogTitle: "Enter a radius",
            ClearMapRadiusPlaceHolder: "Radius (meters)...",
            showClearMapRadius: false,
            ClearMapRadius: "",
        }
    },
    computed: {
        ...mapState(["WeathersList","TimesList","FreezeWeather","FreezeTime","Blackout"]),
    },
    methods: {
        async handleKickReason(text) {
            this.KickReason = text
            await api.post("KickAllPlayers", {Type: "Self", Reason: this.KickReason});
        },
        async handleAnnounceText(text) {
            this.TextAnnouncement = text
            await api.post("ServerAnnouncement", {Type: "Self", Text: this.TextAnnouncement});
        },
        async handleClearMapRadius(text) {
            if (!isNaN(text)) {
                this.ClearMapRadius = Number(text).toFixed(1);
                await api.post("ClearMapAround", {Type: "Self", Radius: this.ClearMapRadius});
            }
        },
        async ClickButton3(buttonName) {
            if (buttonName === "SetWeather") {
                await api.post(buttonName, {Type: "Self", Weather: this.SelectedWeatherName});
            } else if (buttonName === "SetTime") {
                await api.post(buttonName, {Type: "Self", Time: this.SelectedTimeName});
            } else {
                await api.post(buttonName, {Type: "Self"});
            }
        },
        async ToggleButton3(buttonName) {
            this.$store.commit('updateToggle', buttonName);
            await api.post(buttonName, {Type: "Self"});
        },
    }
}
</script>