import api from "@/api/axios";
import DialogInput from "@/views/Components/DialogInput.vue";

export default {
    components: {DialogInput},
    props: {
        playerName: String,
        dialog: Boolean,
        playerId: Number,
        playerData: Object
    },
    data() {
        return {
            dialogValue2: false,
            ActiveCategory: "interact",
            SelectedPedName: "",
            SelectedVehicleName: "",
            WarnReason: "",
            KickReason: "",
            BanReason: "",
            SelectedDuration: "",
            SelectedDurationType: "Hours",
            SelectedTPLocation: "",
            SelectedAccountType: "",
            AccountMoneyValue: 0,
            CustomBanNumber: 0,
            SelectedItem: "",
            ItemAmountValue: 0,
            SelectedJob: "",
            SelectedJobGrade: "",
            SelectedGang: "",
            SelectedGangGrade: "",
            SelectedGroup: "",
            BanDurationTypesList: [
                "Hours",
                "Days",
                "Weeks",
                "Months",
            ],
            BansDurationList: [
                "1 Hour",
                "3 Hours",
                "6 Hours",
                "12 Hours",
                "1 Day",
                "2 Days",
                "3 Days",
                "1 Week",
                "2 Weeks",
                "1 Month",
                "Permanent",
                "Custom",
            ],
            DMMessage: "",
            showDM: false,
        };
    },
    computed: {
        ...mapState(["PedsList","Vehicles","VehiclesList", "Items","Jobs","Gangs","Accounts","GroupRanks","QuickTeleportLocations"]),
    },
    watch: {
        dialog: function(val) {
            this.dialogValue2 = val;
        },
        dialogValue2: function(val) {
            if (!val) {
                this.$emit("dialog-closed");
            }
        }
    },
    methods: {
        async handleDMMessage(text) {
            this.DMMessage = text
            await api.post("DM", {Type: "Player", PlayerId: this.playerId, Message: this.DMMessage});
        },
        numberRule(value) {
            if (!value || /^[0-9]*$/.test(value)) return true;
            return 'Entrez uniquement des chiffres';
        },
        save() {
            this.dialogValue2 = false;
        },
        close() {
            this.dialogValue2 = false;
        },
        setActiveCategory(tabName) {
            this.ActiveCategory = tabName;
        },
        GetWarnsColor(warns) {
            if (warns >= 3) {
                return 'red--text';
            } else if (warns >= 1) {
                return 'orange--text';
            } else {
                return 'green--text';
            }
        },
        async ClickPlayerButton(buttonName) {
            if (buttonName === "SpawnVehicle") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: this.playerId,
                    VehicleName: this.SelectedVehicleName,
                    SpawnInside: true
                });
            } else if (buttonName === "ChangePedModel") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: this.playerId,
                    PedModel: this.SelectedPedName,
                });
            } else if (buttonName === "TeleportTo") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: this.playerId,
                    Location: this.SelectedTPLocation,
                });
            } else if (buttonName === "AddAccountMoney") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: this.playerId,
                    Account: this.SelectedAccountType,
                    Amount: this.AccountMoneyValue,
                });
            } else if (buttonName === "RemoveAccountMoney") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: this.playerId,
                    Account: this.SelectedAccountType,
                    Amount: this.AccountMoneyValue,
                });
            } else if (buttonName === "SetAccountMoney") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: this.playerId,
                    Account: this.SelectedAccountType,
                    Amount: this.AccountMoneyValue,
                });
            } else if (buttonName === "AddItem") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: this.playerId,
                    Item: this.SelectedItem,
                    Count: this.ItemAmountValue,
                });
            } else if (buttonName === "RemoveItem") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: this.playerId,
                    Item: this.SelectedItem,
                    Count: this.ItemAmountValue,
                });
            } else if (buttonName === "SetJob") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: this.playerId,
                    JobLabel: this.SelectedJob,
                    JobGradeLabel: this.SelectedJobGrade,
                });
            } else if (buttonName === "SetGang") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: this.playerId,
                    GangLabel: this.SelectedGang,
                    GangGradeLabel: this.SelectedGangGrade,
                });
            } else if (buttonName === "SetGroup") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: this.playerId,
                    Group: this.SelectedGroup,
                });
            } else if (buttonName === "AddWarn") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: this.playerId,
                    Reason: this.WarnReason,
                });
            } else if (buttonName === "KickPlayer") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: this.playerId,
                    Reason: this.KickReason,
                });
            } else if (buttonName === "BanPlayer") {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: this.playerId,
                    Reason: this.BanReason,
                    Duration: this.SelectedDuration,
                    DurationCustomNumber: this.CustomBanNumber,
                    DurationCustomType: this.SelectedDurationType,
                });
            } else {
                await api.post(buttonName, {
                    Type: "Player",
                    PlayerId: this.playerId,
                });
            }
        },
    },
};
</script>
<style>
.v-text-field__details {
    display: none;
}
.v-application p {
    margin-bottom: auto;
}
</style>