<template>
	<div>
		<v-app dark>
			<Notification ref="notification" />
			<component :is="view" v-if="isVisible"/>
		</v-app>
	</div>
</template>

<script>
import NavBar from "./views/NavBar";
import { mapMutations } from "vuex";

import Notification from './views/Components/Notification.vue';
export default {
	name: "App",
	components: {
		Notification,
	},
	data: () => ({
		view: NavBar,
		isVisible: false,
	}),
	methods: {
		...mapMutations(["setPlayerData", "setConfig"]),
		toggleShow(view) {
			switch (view) {
				case "base":
					this.view = NavBar;
					break;
				default:
					break;
			}
			this.isVisible = !this.isVisible;
		},
		showNotification(data) {
			this.$refs.notification.showNotification({
				title: data.payload.title || 'Notification',
				text: data.payload.text || '',
				timeout:data.payload.timeout || 5000,
				color: data.payload.color || '#1976D2',
				icon: data.payload.icon || 'mdi-bell-ring',
			});
		},
		CopyToClipboard(text) {
			const textarea = document.createElement('textarea')
			textarea.value = text.payload
			document.body.appendChild(textarea)
			textarea.select()
			document.execCommand('copy')
			document.body.removeChild(textarea)
		}
	},
	mounted() {
		this.listener = window.addEventListener("message", (e) => {
			const item = e.data || e.detail;
			if (this[item.type]) this[item.type](item);
		});
	},
	destroyed() {
		window.removeEventListener("message", this.listener);
	},
};
</script>
<style>
::-webkit-scrollbar {
	width: 0;
	display: inline !important;
}

.v-application {
	background: rgb(0, 0, 0, 0.0) !important;
	user-select: none;
}
</style>
